import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, ViewChild } from '@angular/core';

import { PlatformShieldService } from '@playground/shared';

@Component({
  selector: 'playground-rtc',
  templateUrl: './rtc.component.html',
  styleUrls: ['./rtc.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'full-size p-3 overflow-auto'
  }
})
export class RtcComponent {

  @ViewChild('localVideo')
  readonly localVideo!: ElementRef<HTMLVideoElement>;

  @ViewChild('remoteVideo')
  readonly remoteVideo!: ElementRef<HTMLVideoElement>;

  readonly pictureInPictureEnabled = this.platformShield.pictureInPictureEnabled;
  readonly rtcSupported = 'RTCPeerConnection' in this.platformShield.nativeWindow;

  private rtcSender: RTCRtpSender | undefined;
  private sendingConnection: RTCPeerConnection | undefined;

  constructor(
    private readonly cdr: ChangeDetectorRef,
    private readonly platformShield: PlatformShieldService,
  ) {}

  get rtcActive(): boolean {
    return !!this.rtcSender;
  }

  async startRTC(): Promise<void> {
    try {
      const localStream = await this.platformShield.mediaDevices.getUserMedia({
        video: true,
      });

      const sendingConnection = new RTCPeerConnection();
      const receivingConnection = new RTCPeerConnection();

      localStream.getTracks().forEach(track => {
        this.rtcSender = sendingConnection.addTrack(track, localStream);
      });

      receivingConnection.addEventListener('track', event => {
        this.remoteVideo.nativeElement.srcObject = event.streams[0];
      });

      this.handleIceCandidateEvents(sendingConnection, receivingConnection);
      this.createSendingOffer(sendingConnection, receivingConnection);

      this.sendingConnection = sendingConnection;
      this.localVideo.nativeElement.srcObject = localStream;
      this.cdr.markForCheck();
    } catch (error) {
      console.error(error);
    }
  }

  stopRTC(): void {
    if (!this.rtcSender || !this.sendingConnection) {
      return;
    }

    this.sendingConnection.removeTrack(this.rtcSender);
    this.sendingConnection.close();
    this.sendingConnection = undefined;

    this.remoteVideo.nativeElement.srcObject = null;
    this.localVideo.nativeElement.srcObject = null;

    this.exitPictureInPicture();
    this.rtcSender = undefined;
  }

  togglePictureInPicture(): void {
    const nativeDocument = this.platformShield.nativeDocument;

    if (nativeDocument.pictureInPictureElement) {
      nativeDocument.exitPictureInPicture();
    } else if (this.pictureInPictureEnabled) {
      this.localVideo.nativeElement.requestPictureInPicture();
    }
  }

  private exitPictureInPicture(): void {
    if (this.platformShield.nativeDocument.pictureInPictureElement) {
      this.platformShield.nativeDocument.exitPictureInPicture();
    }
  }

  private handleIceCandidateEvents(
    sendingConnection: RTCPeerConnection,
    receivingConnection: RTCPeerConnection,
  ): void {
    sendingConnection.addEventListener('icecandidate', event => {
      if (event.candidate) {
        receivingConnection.addIceCandidate(new RTCIceCandidate(event.candidate));
      }
    });

    receivingConnection.addEventListener('icecandidate', event => {
      if (event.candidate) {
        sendingConnection.addIceCandidate(new RTCIceCandidate(event.candidate));
      }
    });
  }

  private async createSendingOffer(
    sendingConnection: RTCPeerConnection,
    receivingConnection: RTCPeerConnection,
  ): Promise<void> {
    try {
      const sendingDescription = await sendingConnection.createOffer({ offerToReceiveVideo: true });
      sendingConnection.setLocalDescription(sendingDescription);
      receivingConnection.setRemoteDescription(sendingDescription);

      const answerDescription = await receivingConnection.createAnswer({ offerToReceiveVideo: 1 });
      sendingConnection.setRemoteDescription(answerDescription);
      receivingConnection.setLocalDescription(answerDescription);
    } catch (error) {
      console.error('createOffer()/createAnswer() failed ' + error);
    }
  }

}
